<template>
  <div class="blog-list-container" v-loading="loadingShow">
    <h3>文章总数</h3>
    <div class="container"></div>
    <RightList :list="list" @selected="hanldSelected" />
  </div>
</template>
<script>
import { getBlogTypes } from "../../../../api/blog";
import fetchData from "../../../mixins/fetchData";
import RightList from "./rightList";
export default {
  mixins: [fetchData([])],
  //混合数据，会被挂载到vue实例中
  computed: {
    categoryId() {
      return +this.$route.params.categoryId || -1;
    },
    list() {
      const totalArticleCount = this.data.reduce(
        (a, b) => a + b.articleCount,
        0
      );
      //汇总，算出全部分类下所有文章数量，数组中有一个reduce函数，接收两个参数，a，b，然后返回a+b的值
      const result = [
        { name: "全部", id: -1, articleCount: totalArticleCount },
        //这个result就是列表，首先现在列表前面加上一个全部分类，多少片文章就是刚刚通过reduce计算出来的值
        ...this.data,
        //然后把原来data中的数据展开!
      ];
      return result.map((it) => ({
        ...it,
        isSeleted: it.id === this.categoryId,
        //注意，这里的isSeleted是否加上，取决于当前id是不是=== 分类id
        number: `${it.articleCount}篇`,
      }));
    },

    limit() {
      return +this.$route.query.limit || 10;
    },
  },
  components: {
    RightList,
  },
  methods: {
    async fetchData() {
      return await getBlogTypes();
    },
    hanldSelected(item) {
      const query = { page: 1, limit: this.limit };
      if (item.id === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: item.id,
          },
        });
      }
    },
  },
};
</script>
<style lang="less" scoped>
.blog-list-container {
  position: relative;
  width: 210px;
  padding: 0 20px;
  height: 100%;
  h3 {
    height: 50px;
    line-height: 50px;
  }
}
</style>
