<template>
  <MessageArea
    title="评论列表"
    :subTitle="`(${data.rows.length})`"
    :list="data.rows"
    :isListLoading="loadingShow"
    @submit="handelSubmit"
  />
</template>

<script>
import MessageArea from "@/components/MessageArea/";
import fetchData from "../../../mixins/fetchData";
import { getComments, postComment } from "../../../../api/blog";
export default {
  components: {
    MessageArea,
  },
  mixins: [fetchData({ total: 0, rows: [] })],
  data() {
    return {
      page: 1,
      limit: 20,
    };
  },
  created() {
    this.$bus.$on("setSelect", this.hanlderFetchMore);
  },
  computed: {
    hasMore() {
      return this.data.rows.length < this.data.total;
    },
  },
  methods: {
    async fetchData() {
      return await getComments(this.$route.params.id, this.page, this.limit);
    },
    async handelSubmit(formData, callback) {
      const resp = await postComment({
        blogId: this.$route.params.id,
        ...formData,
      });
      this.data.rows.unshift(resp);
      this.data.total++;
      callback("评论成功!");
    },
    hanlderFetchMore(dom) {
      if (this.loadingShow) {
        return;
      }
      const range = 100;
      const des = Math.abs(dom.clientHeight + dom.scrollTop - dom.scrollHeight);
      if (range >= des) {
        this.fetchMore();
      }
    },
    async fetchMore() {
      if (!this.hasMore) {
        return;
      }
      this.loadingShow = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      //重新获取远程数据，将data中到数据总数重新赋值
      this.data.rows = this.data.rows.concat(resp.rows);
      //将往当前data中添加刚刚重新获取到的数据，（通过concat拼接）
      this.loadingShow = false;
    },
  },
};
</script>

