import Home from "@/views/Home";
import About from "@/views/About";
import Blog from "@/views/Blog";
import Message from "@/views/Message";
import Project from "@/views/Project";
import BlogDetail from "@/views/Blog/Detail";
export default [
    { name: "Home", path: "/", component: Home },
    { name: "About", path: "/about", component: About },
    { name: "Blog", path: "/blog", component: Blog },
    { name: "CategoryBlog", path: "/blog/cate/:categoryId", component: Blog },
    //可以匹配/blog/cate/后面的路径，是动态的，地址脸/blog/cate后面跟啥。就是啥!
    { name: "BlogDetail", path: "/blog/:id", component: BlogDetail },
    //匹配该组件，在地址栏中，  localhost://blog/BlogDetail/xxxxx，
    //这里的xxxxx就是:id(动态的)，
    //我们拿到动态id之后，可以通过该id获取对应的评论、详情页信息!
    { name: "Message", path: "/message", component: Message },
    { name: "Project", path: "/project", component: Project },

]