import eventBus from "../../eventBus"
import { shake } from "@/utils"
import defaultGIF from "../assets/default.gif"
import { timeout } from "async"
let images = []
//数组中的每一项是每一个img的dom、和真实的src地址
function setImages() {
    for (const img of images) {
        setImage(img)
    }
}
function setImage(img) {
    img.dom.src = defaultGIF
    const rect = img.dom.getBoundingClientRect();
    const height = rect.height || 100
    const clientHieght = document.documentElement.clientHeight
    if (rect.top >= -height && rect.top <= clientHieght) {
        let tempImage = new Image();
        tempImage.src = img.dom.src
        tempImage.onload = function () {
            img.dom.src = img.src
        }
        images = images.filter((i) => i.dom !== img.dom)
    }
}

function hanlderScroll(dom) {
    setImages();
}

eventBus.$on("setSelect", shake(hanlderScroll, 1000))

export default {
    inserted(el, bindings) {
        const img = {
            dom: el,
            src: bindings.value
        }
        images.push(img)
        setImage(img)
    },
    unbind(el) {
        images = images.filter((img) => img.dom !== el)
    }
}