<template>
  <div class="totop-container" v-show="show" @click="hanlderTop">Top</div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    this.$bus.$on("setSelect", this.hanlderScroll);
  },
  beforeDestroy() {
    this.$bus.$off("setSelect", this.hanlderScroll);
  },
  methods: {
    hanlderScroll(dom) {
      if (!dom) {
        this.show = false;
        return;
      }
      this.show = dom.scrollTop >= 500;
    },
    hanlderTop() {
      this.$bus.$emit("mainScrollTop", 0);
    },
  },
};
</script>

<style lang="less" scoped>
.totop-container {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: fixed;
  right: 50px;
  bottom: 50px;
  color: #fff;
  background: rgb(27, 133, 219);
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  transition: 0.5s;
  font-size: 14px;
  &:hover {
    transform: scale(1.5);
    background: rgba(77, 24, 201, 0.678);
  }
}
</style>
