<template>
  <form
    class="form-container"
    id="slb"
    ref="form"
    @submit.prevent="handleSubmit"
  >
    <div class="form-messageArea-input">
      <input
        type="text"
        placeholder="用户昵称"
        maxlength="10"
        v-model="formData.nickName"
      />
      <span>{{ formData.nickName.length }}/10</span>
    </div>
    <div class="error">{{ error.nickName }}</div>
    <div class="form-messageArea-textarea">
      <textarea
        placeholder="请输入内容"
        maxlength="300"
        v-model="formData.content"
      ></textarea>
      <span>{{ formData.content.length }}/300</span>
    </div>
    <div class="error">{{ error.content }}</div>
    <div class="button">
      <button :disabled="isSubmiting">
        {{ isSubmiting ? "提交中..." : "提交" }}
      </button>
    </div>
  </form>
</template>

<script>
import showMessage from "../../utils/showMessage";
export default {
  data() {
    return {
      isSubmiting: false,
      formData: {
        nickName: "",
        content: "",
      },
      error: {
        nickName: "",
        content: "",
      },
    };
  },
  methods: {
    handleSubmit() {
      this.error.nickName = this.formData.nickName ? "" : "请输入昵称!";
      this.error.content = this.formData.content ? "" : "请输入内容!";
      if (this.error.nickName || this.error.content) {
        return;
      }
      this.isSubmiting = true;
      this.$emit("submit", this.formData, (successMes) => {
        this.formData.nickName = "";
        this.formData.content = "";
        showMessage({
          content: successMes,
          type: "success",
          container: this.$refs.form,

          callback: () => {
            this.isSubmiting = false;
          },
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import "../../styles/var.less";
.form-container {
  width: 100%;
  .form-messageArea-input {
    width: 300px;
    height: 50px;
    position: relative;
  }
  input,
  textarea {
    width: 100%;
    height: 100%;
    outline: none;
    border: 1px dashed rgb(55, 148, 209);
    border-radius: 10px;
    padding: 10px;
    box-sizing: border-box;
    &:focus {
      border-color: @success;
    }
  }
  span {
    position: absolute;
    right: 5px;
    bottom: 5px;
    font-size: 12px;
    color: @lightWords;
  }
  .form-messageArea-textarea {
    height: 300px;
    position: relative;
  }
  .button {
    margin-top: 20px;
    button {
      font-size: 14px;
      outline: none;
      padding: 10px 30px;
      border-radius: 5px;
      background: @primary;
      border: none;
      cursor: pointer;
      color: #fff;
      transition: 0.4s;
      &:hover {
        background: @success;
        transform: scale(1.2);
      }
      &:disabled {
        background: @gray;
        cursor: not-allowed;
        &:hover {
          transform: scale(1);
        }
      }
    }
  }
  .error {
    height: 50px;
    line-height: 50px;
    color: @warn;
  }
}
</style>
