<template>
  <div class="DetailList-container">
    <h3>目录</h3>
    <div class="container"></div>
    <RightList :list="tocWithSelect" @selected="hanldSelected" />
  </div>
</template>

<script>
import RightList from "./rightList";
import { shake } from "@/utils/";
export default {
  components: {
    RightList,
  },
  data() {
    return {
      activeAnchor: "",
    };
  },
  props: {
    toc: {
      type: Array,
      required: true,
    },
  },
  computed: {
    tocWithSelect() {
      const getTOC = (toc = []) => {
        return toc.map((t) => ({
          ...t,
          isSeleted: t.anchor === this.activeAnchor,
          children: getTOC(t.children),
        }));
      };
      return getTOC(this.toc);
    },
    doms() {
      const domArray = [];
      const getNewArray = (item) => {
        for (const t of item) {
          domArray.push(document.getElementById(t.anchor));
          if (t.children && t.children.length) {
            getNewArray(t.children);
          }
        }
      };
      getNewArray(this.toc);
      return domArray;
    },
  },
  created() {
    this.setSelectMain = shake(this.setSelect, 300);
    this.$bus.$on("setSelect", this.setSelectMain);
  },
  destroyed() {
    this.$bus.$off("setSelect", this.setSelectMain);
  },
  methods: {
    hanldSelected(item) {
      location.hash = item.anchor;
    },
    setSelect() {
      const range = 200;
      for (const dom of this.doms) {
        if (!dom) {
          continue;
        }
        const top = dom.getBoundingClientRect().top;
        if (top >= 0 && top <= range) {
          this.activeAnchor = dom.id;
        } else if (top > range) {
          return;
        } else {
          this.activeAnchor = dom.id;
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.DetailList-container {
  position: relative;
  width: 210px;
  padding: 0 20px;
  height: 100%;
  h3 {
    height: 50px;
    line-height: 50px;
  }
}
</style>
