<template>
  <div class="blog-list-container" ref="detailMainContainer">
    <ul class="blog-list-main" v-loading="loadingShow">
      <li
        class="blog-list-main-content"
        v-for="item in data.rows"
        :key="item.id"
      >
        <div class="blog-list-left">
          <RouterLink
            :to="{
              name: 'BlogDetail',
              params: {
                id: item.id,
                //在设置路由的时候，配置了动态路由id，所以，要在点击的时候在链接的后面拼接上指定的id，
                //通过指定的id来显示指定的组件！
              },
            }"
          >
            <img
              v-lazy="item.thumb"
              :alt="item.title"
              :title="item.title"
              class="blog-list-img"
            />
          </RouterLink>
        </div>
        <div class="blog-list-right">
          <h3>
            <RouterLink
              :to="{
                name: 'BlogDetail',
                params: {
                  id: item.id,
                },
              }"
            >
              {{ item.title }}
            </RouterLink>
          </h3>
          <div class="comment">
            <span>日期：{{ formatDate(item.createDate) }}</span>
            <span>浏览：{{ item.scanNumber }}</span>
            <span>评论：{{ item.commentNumber }}</span>
            <RouterLink
              :to="{
                name: 'CategoryBlog',
                params: {
                  categoryId: item.category.id,
                  //由于categoryId是动态的，所以只需要将他改变，页面就可以跳转
                  //item是data中的每一项，item中一个category对象，里面就是对应的id!
                },
              }"
              >{{ item.category.name }}</RouterLink
            >
          </div>
          <div class="decoration">
            <p>{{ item.description }}</p>
          </div>
        </div>
      </li>
    </ul>
    <pager
      :current="routeInfo.page"
      :limit="routeInfo.limit"
      :total="data.total"
      :visibleNumber="10"
      @pageChange="handlePageChange"
      v-if="data.total"
    />
  </div>
</template>

<script>
import pager from "@/components/Pager/";
import fetchData from "@/mixins/fetchData.js";
import { getBlogs } from "../../../../api/blog";
import mainScrollTop from "../../../mixins/mainScroll";
import { formatDate } from "../../../utils/index";
export default {
  mixins: [fetchData({}), mainScrollTop("detailMainContainer")],
  components: {
    pager,
  },
  computed: {
    routeInfo() {
      //拿到当前路由信息（当前是否有分类、当前页码、当前显示多少页）
      const categoryId = +this.$route.params.categoryId || -1;
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      return {
        categoryId,
        page,
        limit,
      };
    },
  },
  methods: {
    formatDate,
    handlePageChange(newPage) {
      const query = { page: newPage, limit: this.routeInfo.limit };
      //当前页码==newPage，当前页容量==新的页容量（通过路由获取的）
      if (this.routeInfo.categoryId == -1) {
        //假设当前categoryId == -1，代表当前显示的是全部分类
        this.$router.push({
          //只需要在匹配路由名称，然后拼接query（新的页码、新的页容量）
          name: "Blog",
          //this.$router.push只要匹配成功，就会在所匹配路由地址的后面拼接query对象的值
          query,
        });
      } else {
        //否则的话，就是有分类id
        this.$router.push({
          name: "CategoryBlog",
          query,
          //匹配CategoryBlog，然后拼接query，
          params: {
            categoryId: this.routeInfo.categoryId,
          },
          //动态的部分可以通过params配置，  新的分类ID就让他 == 当前新id
        });
      }
    },
    async fetchData() {
      return await getBlogs(
        this.routeInfo.page,
        this.routeInfo.limit,
        this.routeInfo.categoryId
      );
    },
  },
  watch: {
    async $route() {
      this.loadingShow = true;
      //当监听到路由发生变化时，先将loadingShow设置为true（即为加载状态）
      this.$refs.detailMainContainer.scrollTop = 0;
      //将滚动条高度设置为0，即显示第一条数据
      this.data = await this.fetchData();
      //重新加载页码数据，这里不需要传递参数，新数据会和旧数据比较，然后将有差异的地方修改，没有差异的默认!
      this.loadingShow = false;
      //加载效果完成!
    },
  },
};
</script>

<style lang="less" scoped>
