        cursor: pointer;
      }
      a:hover {
        display: block;
        height: 100%;
      }
    }
    .blog-list-right {
      width: 74%;
    }
  }
}

.pager-container {
  height: 80px;
}
.blog-list-right {
  h3 a {
    font-size: 26px;
    cursor: pointer;
    transition: 0.4s;
  }
  a:hover {
